@import url(../molecules/article.css);

.authorize {
	font-family: var(--font-family-primary);
	color: var(--color-light-primary);
}

.authorize__body {
	display: grid;
	row-gap: 1rem;
}

@media (width >= 728px) {
	.authorize__body {
		grid-template-columns: 3fr 2fr;
		grid-template-rows: min-content;
		column-gap: 2rem;
	}
}

@media (width < 728px) {
	.authorize__article {
		display: none;
	}
}

@media (width >= 728px) {
	.authorize__article {
		grid-row: span 2;
	}
}

.authorize__link {
	color: inherit;
	text-decoration: none;
	text-align: center;
}

@media (width >= 728px) {
	.authorize__link {
		grid-column: 2 / 3;
	}
}
